import { Injectable } from '@angular/core';
import { Jsonp, URLSearchParams } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { environment } from '../environments/environment';

import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class JsonpService {

  constructor(private jsonp: Jsonp) { }

  /**
   *  
   *   pram: url url
   *   return: Observable  
   */
  requestGet(url: string, ps: URLSearchParams): Observable<any> {
    ps.set('callback', 'JSONP_CALLBACK'); // 

    url = environment.envPath + url; // URL

    return this.jsonp.get(url, { params: ps })
      .map(
      response => {
        return response.json() || {};
      }
      )
      .catch(
      error => {
        // 
        return Observable.throw(error.statusText);
      }
      );
  }

  /**
   *  
   *   pram: url url
   *   return: Observable  
   */
  commonRequestGet(url: string, ps: URLSearchParams): Observable<any> {
    ps.set('callback', 'JSONP_CALLBACK'); // 

    url = environment.envCommonPath + url; // URL

    return this.jsonp.get(url, { params: ps })
      .map(
      response => {
        return response.json() || {};
      }
      )
      .catch(
      error => {
        // 
        return Observable.throw(error.statusText);
      }
      );
  }

}


