    this.updDate = data.updDate // 
    this.updUserNm = data.updUserNm // 

    //     
    this.parentIncidentNo = data.parentIncidentNo; // 
    this.incidentStartDateTime = data.incidentStartDateTime; // 
    this.industryTypeNm = data.industryTypeNm; // 
